@use '../abstract' as *;
@use '../base' as *;


.nav {
  height: 100vh;
  background-color: #3ebfff;
}

.collapsible__heading {

}

.nav__brand {
  display: flex;
  justify-content: space-between;

  a {
    margin: 3rem;
    width: auto;
  }
}

.nav__items {
  margin: 4rem 2rem;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;

  .nav__item {
    padding: 1rem;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;  
    
    a {
    text-decoration: none;
    color: $secondary-color-Dark-grayish-blue;
    transition: all 0.1s ease-in-out;

    &:hover {
      padding: 3px 20px;
      color: $secondary-color-Very-dark-desaturated-blue;
      font-family: $font-stack-heading;
      background-color: $primary-color-Yellow;
      border-radius: 20px;
    }
  }

  }


}

.nav__item:nth-child(1) {
  margin-top: 3rem;
}

.nav__item:nth-last-child(1) {
  margin-bottom: 3rem;
}


.collapsible__content {
  display: none;
}

.collapsible--expanded {

  .collapsible__content {
    display: block;
  }
}